.business-container {
  max-width: var(--container-width);
  margin: 0 auto;
  padding: var(--space-md);
  background-color: var(--bg-primary);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
}

.business-container h2 {
  margin-bottom: 1.5625rem;
  color: var(--text-primary);
}

.business-container h3 {
  margin: 1.875rem 0 1.25rem;
  color: var(--text-primary);
}

.owned-businesses {
  margin-bottom: 1.875rem;
}

.no-businesses {
  text-align: center;
  padding: 1.25rem;
  background-color: #f0f0f0;
  border-radius: 0.5rem;
  margin-bottom: 1.875rem;
}

.no-businesses p {
  color: var(--text-secondary);
  font-style: italic;
}

.businesses-list,
.available-businesses {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.25rem;
}

.business-card {
  background-color: white;
  border-radius: 0.5rem;
  padding: 0.9375rem;
  box-shadow: 0 0.125rem 0.375rem rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  position: relative;
  overflow: hidden;
}

.business-card:hover:not(.cannot-afford):not(.already-owned) {
  transform: translateY(-5px);
  box-shadow: 0 0.25rem 0.625rem rgba(0, 0, 0, 0.15);
}

.business-card.owned {
  background-color: #f0fff0;
  border-left: 0.25rem solid #4CAF50;
}

.business-card.already-owned {
  opacity: 0.6;
  pointer-events: none;
}

.business-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.9375rem;
}

.business-icon {
  font-size: 1.5rem;
  margin-right: 0.9375rem;
  color: #4CAF50;
}

.business-header h3 {
  margin: 0;
  text-align: left;
  font-size: 1.125rem;
}

.business-description {
  color: var(--text-secondary);
  margin-bottom: 0.9375rem;
  font-size: 0.875rem;
}

.business-details {
  margin-bottom: 0.9375rem;
  font-size: 0.875rem;
}

.business-details p {
  margin: 0.1875rem 0;
}

.business-income {
  font-weight: bold;
  color: #4CAF50;
  margin-bottom: 0.9375rem;
}

.upgrade-section {
  background-color: #f0f8ff;
  padding: 0.9375rem;
  border-radius: 0.5rem;
  margin-top: 0.9375rem;
  border-left: 0.25rem solid #2196F3;
}

.upgrade-section h4 {
  margin-top: 0;
  color: #2196F3;
  font-size: 1rem;
}

.max-level {
  text-align: center;
  padding: 0.9375rem;
  background-color: #f0f0f0;
  border-radius: 0.5rem;
  margin-top: 0.9375rem;
  color: #555;
  font-style: italic;
}

.buy-button, 
.upgrade-button {
  width: 100%;
  padding: 0.5rem;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 1.25rem;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: 500;
}

.buy-button:hover:not(:disabled),
.upgrade-button:hover:not(:disabled) {
  background-color: #45a049;
  transform: translateY(-2px);
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
}

.buy-button:disabled,
.upgrade-button:disabled {
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}

.cannot-afford {
  opacity: 0.7;
}

.discount,
.bonus {
  color: #4CAF50;
  font-weight: bold;
  font-size: 0.8125rem;
}

.roi {
  font-weight: bold;
  color: #4CAF50;
}

/* Filter Controls */
.filter-controls {
  margin-bottom: 1.25rem;
  background-color: var(--bg-tertiary);
  padding: 0.9375rem;
  border-radius: var(--radius-lg);
}

.filter-label {
  display: block;
  margin-bottom: var(--space-sm);
  font-weight: var(--weight-semibold);
  color: var(--text-secondary);
}

.filter-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
}

.filter-buttons button {
  background: none;
  border: none;
  padding: 0.125rem;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: var(--radius-md);
}

.filter-buttons button:hover {
  transform: translateY(-2px);
} 